import React, { useState, useEffect } from 'react'
import BodyItems from '../BodyItems/BodyItems'
import './Body.css'
import axios from 'axios'
import CopyToClipboard from 'react-copy-to-clipboard'

const Body = () => {
  const [inputValue, setInputValue] = useState('')
  const [shortenedLink, setShortenedLink] = useState('')
  const [copied, setCopied] = useState(false)

  const getData = () => {
    axios(`https://api.shrtco.de/v2/shorten?url=${inputValue}`, {}).then(
      response => {
        const data = response.data
        console.log(data)
        setShortenedLink(data.result.full_short_link)
      }
    )
.catch(e => {
      console.log(e)
    })
  }

  console.log(shortenedLink)

  useEffect(() => {
    const timer = setTimeout(() => {
      setCopied(false)
    }, 1000)
    return () => clearTimeout(timer)
  }, [copied])

  return (
    <div className='body_container'>
      <div className='body_items'>
        <input
          type='text'
          placeholder='Enter link to be shortened'
          value={inputValue}
          onChange={e => setInputValue(e.target.value)}
        />

        <button className='body_btn' onClick={() => getData()}>
          Shorten it
        </button>
      </div>
      <div className='body_inputs'>
        {shortenedLink ? (
          <div className='input'>
            <p className='shortened_paragraph'>{shortenedLink}</p>
            <CopyToClipboard
              text={shortenedLink}
              onCopy={() => setCopied(true)}
            >
              <button className='body_inputs_btn'>
                {copied ? 'copied' : 'copy'}
                {setShortenedLink('')}
              </button>
            </CopyToClipboard>
          </div>
        ) : null}
      </div>

      <BodyItems />
    </div>
  )
}

export default Body
